import "./ticketBoard.css";
import React, { FormEvent, useEffect, useRef, useState } from "react";
import {
    getDatabase,
    ref,
    push,
    onValue,
    update,
    remove,
} from "firebase/database";
import ContentEditable, { ContentEditableEvent } from "react-contenteditable";
import firebase from "firebase/compat/app";
import StyledFirebaseAuth from "./StyledFirebaseAuth";

const firebaseConfig = {
    apiKey: "AIzaSyBT83dncPlqAbXs13ugtzkP9of__VEbuic",
    authDomain: "ludeoliver.firebaseapp.com",
    projectId: "ludeoliver",
    storageBucket: "ludeoliver.appspot.com",
    messagingSenderId: "500785507980",
    appId: "1:500785507980:web:0a22eaea4d8c7ab9e855a9",
    measurementId: "G-DSSTHVPQ9Y",
};

// Configure FirebaseUI.
const uiConfig = {
    // Popup signin flow rather than redirect flow.
    signInFlow: "popup",
    // We will display Google and Facebook as auth providers.
    signInOptions: [
        // firebase.auth.GoogleAuthProvider.PROVIDER_ID,
        firebase.auth.EmailAuthProvider.PROVIDER_ID,
    ],
    callbacks: {
        // Avoid redirects after sign-in.
        signInSuccessWithAuthResult: () => false,
    },
};

const app = firebase.initializeApp(firebaseConfig);
const db = getDatabase();
const dbRef = ref(db, "tickets");

function TicketForm(props: any) {
    const [title, setTitle] = useState("");
    const titleOnChange = (t: string) => {
        setTitle(t);
    };
    const handleSubmit = (event: FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        push(dbRef, {
            title: title,
            is_complete: false,
        });
        setTitle("");
    };
    return (
        <form onSubmit={handleSubmit} id="ticketForm">
            <input
                id="content-input"
                type="text"
                name="title"
                placeholder="Add a new wish"
                value={title}
                onChange={(e) => titleOnChange(e.target.value)}
            />
            <input type="submit" value="Add" id="addWish-button" />
        </form>
    );
}

function Ticket(props: any) {
    const taskRef = ref(db, "tickets/" + props.value.childKey);
    const { title, is_complete } = props.value.childData;
    const text = useRef(title);

    const updateComplete = () => {
        update(taskRef, {
            is_complete: !is_complete,
        });
    };

    const updateTitle = (newTitle: string) => {
        update(taskRef, {
            title: newTitle,
        });
    };

    const handleChange = (event: ContentEditableEvent) => {
        text.current = event.target.value;
    };

    const handleBlur = () => {
        updateTitle(text.current);
    };

    const handleDelete = () => {
        remove(taskRef);
    };

    return (
        <li>
            <div className="ticket">
                <div className="left-row">
                    <input
                        type="checkbox"
                        className="ticket-checkbox"
                        onChange={updateComplete}
                        checked={is_complete}
                    />

                    <ContentEditable
                        html={text.current}
                        onChange={handleChange}
                        onBlur={handleBlur}
                        className={
                            is_complete
                                ? "checked-content"
                                : "unchecked-content"
                        }
                    />
                </div>

                <button className="deleteButton" onClick={() => handleDelete()}>
                    X
                </button>
            </div>
        </li>
    );
}

function TicketList(props: any) {
    const [ticketList, setTicketList] = useState(
        [] as { childKey: string; childData: string }[]
    );
    useEffect(() => {
        setTimeout(() => {
            const newTicketList: { childKey: string; childData: string }[] = [];
            onValue(dbRef, (snapshot) => {
                snapshot.forEach((childSnapshot) => {
                    const childKey = childSnapshot.key;
                    const childData = childSnapshot.val();
                    newTicketList.push({
                        childKey: childKey as string,
                        childData,
                    });
                });
                setTicketList(newTicketList);
            });
        }, 500);
    });
    return (
        <div id="TicketList">
            <ul>
                {ticketList.length !== 0
                    ? ticketList.map((value, _) => (
                          <Ticket key={value.childKey} value={value} />
                      ))
                    : ""}
            </ul>
        </div>
    );
}

function TicketBoard() {
    const [isSignedIn, setIsSignedIn] = useState(false); // Local signed-in state.

    // Listen to the Firebase Auth state and set the local state.
    useEffect(() => {
        const unregisterAuthObserver = firebase
            .auth()
            .onAuthStateChanged((user) => {
                setIsSignedIn(!!user);
            });
        return () => unregisterAuthObserver(); // Make sure we un-register Firebase observers when the component unmounts.
    }, []);

    if (!isSignedIn) {
        return (
            <section id="sign-in">
                <p>Please sign-in:</p>
                <StyledFirebaseAuth
                    uiConfig={uiConfig}
                    firebaseAuth={firebase.auth()}
                />
            </section>
        );
    }
    return (
        <section id="wishlist">
            <div className="card">
                <h2>Wishlist ✨</h2>
                <TicketForm />
                <TicketList />
            </div>
        </section>
    );
}
export default TicketBoard;
